import React, {Component} from 'react'
import { Chart } from "react-google-charts"
import styled from "styled-components"
import { BrowserView, MobileView } from 'react-device-detect'
import moment from 'moment'
import { connect } from 'react-redux'
import { fs } from '../../../../../../config/fbConfig'

import { Button, ButtonGroup } from 'react-bootstrap';
import "../../../../Pages.css"
import "../../../../../../App.css";
import {Link} from "react-router-dom"
import {Card} from "react-bootstrap"

const time = moment().format("YYYY")

class Chart62 extends Component {

    state = {
        uid: this.props.auth.uid,
        janGHGBusiness: 0,
        febGHGBusiness: 0,
        marGHGBusiness: 0,
        aprGHGBusiness: 0,
        mayGHGBusiness: 0,
        junGHGBusiness: 0,
        julGHGBusiness: 0,
        augGHGBusiness: 0,
        sepGHGBusiness: 0,
        octGHGBusiness: 0,
        novGHGBusiness: 0,
        decGHGBusiness: 0,
    }

    fetchData = async () => {
        fs.collection('data').doc(this.state.uid).collection('writtenFoodWasteData')
          .get()
          .then( snapshot => {

            snapshot.forEach(doc => {

                var year = doc.data().YEAR
                var month = doc.data().MONTH
                var ghg = doc.data().GHG
                // var isSurplus = doc.data().EDIBLEORINEDIBLE

                var st = doc.data().SUBMISSIONTYPE

                // var carbCon = doc.data().CARBSCONTENT
                // var proCon = doc.data().PROTEINCONTENT
                // var fatCon = doc.data().FATCONTENT
                // var fibCon = doc.data().FIBRECONTENT

                if (year === time && month === "Jan" && st === "Waste Business"){
                    this.setState( (prevState) => ({
                      janGHGBusiness: prevState.janGHGBusiness += ghg
                    }));
                  } else if (year === time && month === "Feb" && st === "Waste Business"){
                    this.setState( (prevState) => ({
                      febGHGBusiness: prevState.febGHGBusiness += ghg
                    }));
                  } else if (year === time && month === "Mar" && st === "Waste Business"){
                    this.setState( (prevState) => ({
                      marGHGBusiness: prevState.marGHGBusiness += ghg
                    }));
                  } else if (year === time && month === "Apr" && st === "Waste Business"){
                    this.setState( (prevState) => ({
                      aprGHGBusiness: prevState.aprGHGBusiness += ghg
                    }));
                  } else if (year === time && month === "May" && st === "Waste Business"){
                    this.setState( (prevState) => ({
                      mayGHGBusiness: prevState.mayGHGBusiness += ghg
                    }));
                  } else if (year === time && month === "Jun" && st === "Waste Business"){
                    this.setState( (prevState) => ({
                      junGHGBusiness: prevState.junGHGBusiness += ghg
                    }));
                  } else if (year === time && month === "Jul" && st === "Waste Business"){
                    this.setState( (prevState) => ({
                      julGHGBusiness: prevState.julGHGBusiness += ghg
                    }));
                  } else if (year === time && month === "Aug" && st === "Waste Business"){
                    this.setState( (prevState) => ({
                      augGHGBusiness: prevState.augGHGBusiness += ghg
                    }));
                  } else if (year === time && month === "Sep" && st === "Waste Business"){
                    this.setState( (prevState) => ({
                      sepGHGBusiness: prevState.sepGHGBusiness += ghg
                    }));
                  } else if (year === time && month === "Oct" && st === "Waste Business"){
                    this.setState( (prevState) => ({
                      octGHGBusiness: prevState.octGHGBusiness += ghg
                    }));
                  } else if (year === time && month === "Nov" && st === "Waste Business"){
                    this.setState( (prevState) => ({
                      novGHGBusiness: prevState.novGHGBusiness += ghg
                    }));
                  } else if (year === time && month === "Dec" && st === "Waste Business"){
                    this.setState( (prevState) => ({
                      decGHGBusiness: prevState.decGHGBusiness += ghg
                    }));
                  }
                  
            })

          })
          .catch(error => console.log(error))
    }

    componentDidMount(){
        this.fetchData();
    }

    render(){

        const {auth} = this.props;

        return(
            <React.Fragment className="row">

                <br/>
                <br/>
                <br/>

                <BrowserView>
                    {/* <ChartStyle> */}
                    <div style={{height: "120%", marginBottom: "2.5%", marginLeft: "10%"}}>
                        <Chart className="bar-chart"
                            width={'78vw'}
                            height={'600px'}
                            chartType="ColumnChart"
                            loader={<div>Loading Chart</div>}
                            data={[
                                ['Month', 'Food Wastage GHG'],
                                ['January', this.state.janGHGBusiness],
                                ['February', this.state.febGHGBusiness],
                                ['March', this.state.marGHGBusiness],
                                ['April', this.state.aprGHGBusiness],
                                ['May', this.state.mayGHGBusiness],
                                ['June', this.state.junGHGBusiness],
                                ['July', this.state.julGHGBusiness],
                                ['August', this.state.augGHGBusiness],
                                ['September', this.state.sepGHGBusiness],
                                ['October', this.state.octGHGBusiness],
                                ['November', this.state.novGHGBusiness],
                                ['December', this.state.decGHGBusiness],
                            ]}
                            options={{
                                title: 'This year\'s Food Wastage GHG Performance (' + time + ', Business)',
                                chartArea: {width: '75%'},
                                colors: ['#aab41e'],
                                hAxis: {
                                    title: 'Month of ' + time,
                                    minValue: 0,
                                },
                                vAxis: {
                                    title: 'GHG Emissions of Food Wastage (kg co2)'
                                }
                            }}
                            legendToggle
                        />
                      </div>
                    {/* </ChartStyle> */}

                    <div style={{height: "40px", marginBottom: "10%"}}>
                        <Card  style={{width: '78vw', height: '35px', marginBottom: "10%", marginLeft: '10%', padding: "2.5% 5% 2.5% 5%", justifyContent: "center"}}>
                        <ButtonGroup>
                            <Button style={{width: "15%"}} className="custom-btn" as={Link} to="/chart/monthGHGBusiness">View Previous (Monthly GHG)</Button>
                            <Button style={{width: "7.5%"}} className="custom-btn" as={Link} to="/account">Back</Button>
                            <Button style={{width: "15%"}} disabled>View Next</Button>
                        </ButtonGroup>
                        </Card>
                    </div>

                </BrowserView>

                <MobileView>
                    {/* <ChartStyle> */}
                    <div style={{height: "120%", marginBottom: "2.5%", marginLeft: "5.5%"}}>
                        <Chart className="bar-chart"
                            width={'90vw'}
                            height={'600px'}
                            chartType="ColumnChart"
                            loader={<div>Loading Chart</div>}
                            data={[
                                ['Month', 'GHG '],
                                ['Jan', this.state.janGHGBusiness],
                                ['Feb', this.state.febGHGBusiness],
                                ['Mar', this.state.marGHGBusiness],
                                ['Apr', this.state.aprGHGBusiness],
                                ['May', this.state.mayGHGBusiness],
                                ['Jun', this.state.junGHGBusiness],
                                ['Jul', this.state.julGHGBusiness],
                                ['Aug', this.state.augGHGBusiness],
                                ['Sep', this.state.sepGHGBusiness],
                                ['Oct', this.state.octGHGBusiness],
                                ['Nov', this.state.novGHGBusiness],
                                ['Dec', this.state.decGHGBusiness],
                            ]}
                            options={{
                                title: 'Food Wastage GHG Performance (' + time + ', Business)',
                                chartArea: {width: '62.5%'},
                                colors: ['#aab41e'],
                                legend: "none",
                                hAxis: {
                                    title: 'Month of ' + time,
                                    minValue: 0,
                                },
                                vAxis: {
                                    title: 'GHG Emissions of Food Wastage (kg co2)'
                                }
                            }}
                        />
                      </div>
                    {/* </ChartStyle> */}

                    <div style={{height: "95px", marginBottom: "15%"}}>
                        <Card  style={{width: '90vw', height: '95px', marginBottom: "15%", marginLeft: '5.5%', padding: "2.5% 5% 2.5% 5%", justifyContent: "center"}}>
                        <ButtonGroup>
                            <Button style={{width: "15%"}} className="custom-btn" as={Link} to="/chart/monthGHGBusiness">Prev</Button>
                            <Button style={{width: "7.5%"}} className="custom-btn" as={Link} to="/account">Back</Button>
                            <Button style={{width: "15%"}} disabled>Next</Button>
                        </ButtonGroup>
                        </Card>
                    </div>

                </MobileView>

            </React.Fragment>
        )

    }

}

const ChartStyle = styled.div`
  .bar-chart{
    position: absolute;
    left: 50%;
    right: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
  }

  .area-chart{
    padding: 10px;
  }
`;

const mapStateToProps = (state) => { 
  return{
      auth: state.firebase.auth,
  }
}

export default connect(mapStateToProps, null)(Chart62);
